.item img{
	max-width: 50px;
	height: 50px;
	border-radius: 50%; 
	background-color: rgb(221, 250, 75);
}
.item {
	/* border: 1px #dadada solid; */
	border-radius: 13px;
	margin: 0 0 10px 0;
	background-color: #fff;
	padding: 15px 25px;
}
.profilePhoto {
	display: flex;
}

.profilePhotoName {
	color: #101286;
	font-weight: 500;
	margin: 0 0 0 8px;
}
.postMessage {
	margin: 20px 0 ;
}
.postLike {
	background-color:rgba(239, 239, 239, 0.88);
	border-radius: 13px;
	width: 70px;
	height: 30px;
	text-align: center;
	border: 1px #dadada solid;
	display: flex;
	justify-content: space-around;
	align-items: center;
}