.img1 img{
	width: 100%;
	height: 250px;
	/* background-image: url(https://img.freepik.com/free-photo/tropical-beach_74190-188.jpg); */
	background-size: cover;
	background-position: bottom;
	border-radius: 13px;
	/* border: 1px rgba(26, 138, 207, 0.856) solid; */
}
.mainNameBlock{
	margin: 8px 0;
}
.mainPhotoBlock1 {
	display: inline-block;
	position: relative;
	width: 100%;
	height: 110px;
	border: 1px #dadada solid;
	border-radius: 13px;
}
.mainPhoto {
	position: absolute;
	top: -60%;
	left: 0px;
	max-width: 150px;
	max-height: 150px;
	border-radius: 50%;
	border: 3px #fff solid;
}
.mainPhotoBlock2 {
	position: absolute;
	top: 0;
	left: 165px;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
.mainPhotoBlock2Name {
	font-size: 2rem;
	font-weight: 700;
}
.mainPhotoButton {
	padding: 0 0 8px 0;
}
.profileInfoForm {
	border: 1px #dadada solid;
	border-radius: 13px;
	padding: 10px 15px;
}
.profileStatus {
	padding: 0 0 15px 0;
}
.editProfileButton {
	float: right;
	border: 1px #dadada solid;
	border-radius: 13px;
}
.profileSection {
	margin: 0 0 10px 0;
}
.contact {
	padding: 0 0 0 10px;
}

